import React, { useEffect } from 'react';
import CustomTextField from '../../Layout/CustomTextField';

const NameOfListedEntity = ({ id, name_of_listed_entity, onChangeIn, errMsgs } ) => {

    useEffect(()=>{
        return ()=>{
          onChangeIn({ name:'name_of_listed_entity', value:'' });
        }
      },[]);

  return (
    <>
        <CustomTextField 
            type={"form-input-full-width u-margin-bottom-0 u-padding-y"}
            label="Please advise a name of listed entity"
            placeholder="Please advise a name of listed entity"
            id={id}
            asterisk="*"
            name="name_of_listed_entity"
            value={name_of_listed_entity}
            onChangeFn={onChangeIn}
            formErrors={errMsgs}
        />
    </>
  )
}

export default NameOfListedEntity;