import React, { useEffect } from 'react';
import CustomTextField from '../../Layout/CustomTextField';

const NameOfExemptPublicAuth = ({ id, name_of_exempt_public_authority, onChangeIn, errMsgs }) => {

    useEffect(()=>{
        return ()=>{
          onChangeIn({ name:'name_of_exempt_public_authority', value:'' });
        }
      },[]);

  return (
    <>
        <CustomTextField
            type={"form-input-full-width u-margin-bottom-0 u-padding-y"}
            label="Please advise name of exempt public authority"
            placeholder="Please advise name of exempt public authority"
            id={id}
            asterisk="*"
            name="name_of_exempt_public_authority"
            value={name_of_exempt_public_authority}
            onChangeFn={onChangeIn}
            formErrors={errMsgs}
        />
    </>
  )
}

export default NameOfExemptPublicAuth;