import React from 'react';
import { useSelector } from 'react-redux';
import CustomRadioInput from '../../../Layout/CustomRadioInput';
import AFSLNumber from '../AFSLNumber';
import { I_AM_APPLYING_FOR_AU_H, I_HAVE_NET_ASSETS_OF_AT_LEAST_AU_H, I_HAVE_EARNED_AT_LEAST_AU_H, I_HAVE_OR_CONTROL_GROSS_ASSETS_OF_AT_LEAST_AU_H, WE_ARE_A_LISTED_ENTITY_OR_A_RELATED_BODY_H, I_WE_ARE_AN_EXEMPT_PUBLIC_AUTHORITY_H, I_HOLD_A_AUSTRALIAN_FINANCIAL_SERVICES_LICENCE_H } from '../../../../../constants';
import { renderErrorTxt } from '../../../../../utils/validations/Investor-Application-Form/formErrorUtils';
import NameOfListedEntity from '../NameOfListedEntity';
import NameOfExemptPublicAuth from '../NameOfExemptPublicAuth';

const InvestorCategoryTypes = ({ investorData, invId, investorIndex, onChangeInvstrData, itemIndex }) => {
  const invFormStatus = useSelector((store) => store.invFormStatus);

  const errMsgs = {
    // investor_id : "",
    // invform_accountant_certificate_detail_id : "",
    category : invFormStatus.errors?.category? invFormStatus.errors?.category[itemIndex]: [],
    afsl : invFormStatus.errors?.afsl? invFormStatus.errors?.afsl[itemIndex]: [],
    name_of_exempt_public_authority: invFormStatus.errors?.name_of_exempt_public_authority? invFormStatus.errors?.name_of_exempt_public_authority[itemIndex]: [],
    name_of_listed_entity : invFormStatus.errors?.name_of_listed_entity? invFormStatus.errors?.name_of_listed_entity[itemIndex]: [],
  }

    const onChangeRadioFn = ({ name, value }) =>{ 
      const convrtToNum = parseInt(value);
      onChangeInvstrData({ name: "category", value: convrtToNum, investorId: invId });
    }
 
    const onChangeIn = ({name, value}) =>{ 
      onChangeInvstrData({name, value, investorId: invId });
    }

  return (
    <div className="section__container--box" key={investorIndex}>
      <h5 className="u-padding-10y">{`Investor ${investorIndex}`}</h5>

            <div>
              <CustomRadioInput 
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-5L-units`}
                content="I am applying for AU$500,000 or more worth of Units individually"
                val={I_AM_APPLYING_FOR_AU_H}
                checked={investorData?.category===I_AM_APPLYING_FOR_AU_H}
                onChangeFn={onChangeRadioFn}
              />
                
              <CustomRadioInput 
                type=""
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-25m-units`}
                content="I have net assets of at least AU$2.5 million (Please upload Qualified Accountant Certificate)"
                val={I_HAVE_NET_ASSETS_OF_AT_LEAST_AU_H}
                checked={investorData?.category===I_HAVE_NET_ASSETS_OF_AT_LEAST_AU_H}
                onChangeFn={onChangeRadioFn}
              />

              <CustomRadioInput 
                type="row-input"
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-25L-income`}
                content="I have earned at least AU$250,000 per annum gross income for each of the last two (2) financial years  (Please upload Qualified Accountant Certificate)"
                val={I_HAVE_EARNED_AT_LEAST_AU_H}
                checked={investorData?.category===I_HAVE_EARNED_AT_LEAST_AU_H}
                onChangeFn={onChangeRadioFn}
              />

               <CustomRadioInput
                type=""
                name={`investor-${investorIndex}-category`} 
                id={`investor-${investorIndex}-10m-assets`} 
                content="I have or control gross assets of at least AU$10 million (Please upload Qualified Accountant Certificate)"
                val={I_HAVE_OR_CONTROL_GROSS_ASSETS_OF_AT_LEAST_AU_H}
                checked={investorData?.category===I_HAVE_OR_CONTROL_GROSS_ASSETS_OF_AT_LEAST_AU_H}
                onChangeFn={onChangeRadioFn}
              />

              <CustomRadioInput
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-listed-rel-body`} 
                content="We are a listed entity or a related body corporate of a listed entity"
                val={WE_ARE_A_LISTED_ENTITY_OR_A_RELATED_BODY_H}
                checked={investorData?.category===WE_ARE_A_LISTED_ENTITY_OR_A_RELATED_BODY_H}
                onChangeFn={onChangeRadioFn}
              />

              {investorData?.category === WE_ARE_A_LISTED_ENTITY_OR_A_RELATED_BODY_H?
              <NameOfListedEntity id={invId} key={invId} name_of_listed_entity={investorData?.name_of_listed_entity} onChangeIn={onChangeIn} errMsgs={errMsgs}/> : null}

              <CustomRadioInput
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-exemp-public`}
                content="I/we are an exempt public authority (or a wholly owned subsidiary of one)"
                val={I_WE_ARE_AN_EXEMPT_PUBLIC_AUTHORITY_H}
                checked={investorData?.category===I_WE_ARE_AN_EXEMPT_PUBLIC_AUTHORITY_H}
                onChangeFn={onChangeRadioFn}
              />

              {investorData?.category === I_WE_ARE_AN_EXEMPT_PUBLIC_AUTHORITY_H?
              <NameOfExemptPublicAuth id={invId} key={invId} name_of_exempt_public_authority={investorData?.name_of_exempt_public_authority} onChangeIn={onChangeIn} errMsgs={errMsgs}/>: null}

              <CustomRadioInput
                name={`investor-${investorIndex}-category`}
                id={`investor-${investorIndex}-afs-licence`}
                content="I hold a Australian Financial Services Licence (AFSL) or a subsidiary of an AFSL holder"
                val={I_HOLD_A_AUSTRALIAN_FINANCIAL_SERVICES_LICENCE_H}
                checked={investorData?.category===I_HOLD_A_AUSTRALIAN_FINANCIAL_SERVICES_LICENCE_H}
                onChangeFn={onChangeRadioFn}
              />

              {investorData?.category===I_HOLD_A_AUSTRALIAN_FINANCIAL_SERVICES_LICENCE_H? 
              <AFSLNumber id={invId} key={invId} afsl={investorData?.afsl} onChangeIn={onChangeIn} errMsgs={errMsgs}/>: null}

              {errMsgs?.category?.length? <div className="input__desc">
                {renderErrorTxt(errMsgs.category)}
              </div>: null}
            </div>     
        </div>
  )
}

export default InvestorCategoryTypes;