import React, {useEffect} from 'react';
import CustomTextField from '../../Layout/CustomTextField';

const AFSLNumber = ({ id, afsl, onChangeIn, errMsgs }) => {
  
  useEffect(()=>{
    return ()=>{
      onChangeIn({ name:'afsl', value:'' });
    }
  },[])
  
  return (
    <>
        <CustomTextField
            type={"form-input-full-width u-margin-bottom-0 u-padding-y"} 
            label="AFSL Number"
            asterisk={'*'}
            id={id}
            placeholder="AFSL Number"
            name="afsl"
            value={afsl}
            onChangeFn={onChangeIn}
            formErrors={errMsgs}
        />
    </>
  )
}

export default AFSLNumber